<template>
  <div>
    <!-- header begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-search">
        <div class="header-search" @click="goSerarch()">
          <search-box ref="searchBox" @query="onQueryChange" @goSearch="goSearch" />
        </div>
      </div>
    </div> -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left">
        <svg aria-hidden="true" class="icon" @click="goback()">
          <use xlink:href="#iconicon-back" />
        </svg>
      </div>
      <div class="nav-bar-title">商品分类</div>
    </div> -->
    <headerBar :header-data="headerData" />
    <!-- header end -->
    <div :key="key" class="container">
      <!--  商品分类 begin -->
      <div class="category-content">
        <van-tree-select
          :items="items"
          :main-active-index.sync="activeIndex"
          @click-nav="onClick()"
        >
          <template slot="content" class="category-right">
            <div v-for="(item,index) of cateList[activeIndex]" id="cate" :key="index" class="category-list">
              <h2 ref="cate" class="tit">{{ item.titleData.name }}</h2>
              <ul ref="cate">
                <li v-for="(tag,indexs) of item.tags" ref="cate" :key="indexs" @click="gogoodslist(tag)">{{ tag.name }}</li>
              </ul>
            </div>
          </template>
        </van-tree-select>
      </div>
    <!-- 商品分类  end -->
    </div>
    <FooterNavMall />
  </div>
</template>

<script land="script" scoped>
import { category } from '@/store/module.js'
import headerBar from '@/components/header/headerBar.vue'
import FooterNavMall from '@/components/footerNav/footerNavMall.vue'
// import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'MallCategory',
  components: {
    FooterNavMall,
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      key: 0,
      active1: 'active',
      activeIndex: 0,
      items: [],
      cateList: [],
      headerData: {
        title: '商品分类',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  created() {
    this.getSpMenu()
  },
  methods: {
    onClick() {
      this.key++
    },
    goback() {
      this.$router.back()
    },
    getSpMenu() {
      category.data = { dsbs: this.dsbs }
      this.asyncApiModule(category).then(response => {
        const allNav = {
          id: 'ALL',
          dsbs: '',
          name: '全部',
          logo: 'e02c958ec7b7464c8aa2808cf57dac3a',
          zqtp: '99e3d39705ff40d2aa50cb5655cf47b9'
        }
        if (response.navs.length === 0) {
          this.navs = []
        } else {
          this.navs = response.navs
          this.navs.unshift(allNav)
        }
        for (let i = 0; i < response.navBody.length; i++) {
          const res = response.navBody[i]
          this.items.push({ text: res.navSides[0].name })
          this.cateList.push(res.classNavs)
        }
        this.$store.dispatch('home/setdsbs', this.dsbs)
        this.$store.dispatch('home/setids', '')
        this.navBody = response.navBody
      })
    },
    gogoodslist(tag) {
      this.$router.push({
        name: 'GoodsList',
        params: {
          category_id: tag.id
        }
      })
    }
  }

}
</script>

<style lang="css" scoped>

</style>
